<template>
  <div class="editor">
    <div class="wrap_select_toggle">
      <div class="select">
        <div class="main">
          <select v-model="main" required @change="onResetSubMenu">
            <option selected disabled hidden value="">main menu</option>
            <option v-for="mainmenu in mainMenus" :key="mainmenu" :value="mainmenu">{{ mainmenu }}</option>
          </select>
        </div>

        <div class="sub">
          <select v-model="sub" required :disabled="!main">
            <option selected disabled hidden value="">sub menu</option>
            <option v-for="submenu in subMenus" :key="submenu" :value="submenu">{{ submenu }}</option>
          </select>
        </div>

        <div class="category">
          <select v-model="category" :disabled="!sub">
            <option selected disabled hidden value="">category</option>
            <option v-for="ctg in categories" :key="ctg" :value="ctg">{{ ctg }}</option>
          </select>
        </div>
      </div>

      <div class="wrap_toggle">
        <Toggle :isPublic="isPublic" @updateIsPublic="onUpdateIsPublic" />
      </div>
    </div>

    <div class="wrap_title_file-add-btn">
      <div class="title">
        <input
          type="text"
          v-model="title"
          placeholder="제목을 입력하세요."
          onfocus="this.placeholder=''"
          onblur="this.placeholder='제목을 입력하세요.'"
        />
      </div>
      <div class="images_add_btn">
        <label for="upload_input" class="upload_label">사진 불러오기</label>
        <input type="file" id="upload_input" @change="onUploadImages" multiple />
      </div>
    </div>

    <div class="content">
      <textarea 
        id="content-el"
        v-model="content"
        placeholder="당신의 이야기를 적어보세요..."
        @keydown="onUpdateCanLeavePage(false)"></textarea>
      <markdown
        class="markdown"
        :source="content"
        :plugins="plugins"
        :breaks="true"
        :xhtmlOut="true"
        :typographer="true"
      />
    </div>

    <div class="images" v-if="images.length">
      <ul>
        <li v-for="(image, index) in images" :key="image.serverFileName">
          <div class="wrap_thumbnail">
            <img
              src="https://news.nateimg.co.kr/orgImg/cc/2021/07/23/4159_10727_3352.png"
              :class="fileState.fileIndex === index ? 'selected_thumbnail' : ''"
              @click="onSelectImage(image, index)"
            />
            <!--
            <img :src="`${fileState.fileUrl}${image.serverFileName}`" :class="fileState.fileIndex === index ? 'selectedThumbnail' : ''" @click="onSelectImage(image, index)" />
            -->
            <button class="image_del_btn" @click="onDeleteImage(image, index)">
              <i class="material-symbols-outlined"> do_not_disturb_on </i>
            </button>
          </div>
        </li>
      </ul>
    </div>

    <div class="wrap_btns">
      <div class="wrap_left">
        <Button
          class="btn_image_insert"
          :content="'사진 첨부'"
          :size="'md'"
          :borderColor="'primary-dark'"
          :rounded="true"
          :disabled="isLoading"
          @onClick="onInsertImage"
        ></Button>
        <Button
          v-show="images.length"
          class="btn_image_clear"
          :content="'사진 모두 제거'"
          :size="'md'"
          :borderColor="'primary-dark'"
          :rounded="true"
          :disabled="isLoading"
          @onClick="onClearImages"
        ></Button>
      </div>
      <div class="wrap_right">
        <div class="wrap_isLoading">
          <Transition name="isLoading">
            <span class="isLoading" v-if="isLoading">
              <i class="material-symbols-outlined">hourglass_empty</i>{{ `자동 저장중...${percentage}` }}
            </span>
          </Transition>
        </div>
        <div class="wrap_auto-save">
          <input type="checkbox" id="auto-save" />
          <label for="auto-save">자동 저장 여부</label>
        </div>
        <Button
          class="btn_submit"
          :content="'글 등록'"
          :size="'md'"
          :bgColor="'primary-dark'"
          :customBgColor="'red'"
          :custom="true"
          :rounded="true"
          :disabled="isLoading"
          @onClick="onUpdatePost"
        ></Button>
      </div>
    </div>
  </div>
  <Dialog ref="Dialog"></Dialog>
</template>

<script>
  import {
    defineComponent,
    ref,
    reactive,
    computed,
    watchEffect,
    onBeforeMount,
    onMounted,
    onUnmounted,
  } from 'vue'
  import { useRouter, useRoute, onBeforeRouteLeave } from 'vue-router'
  import { useStore } from 'vuex'
  import Markdown from 'vue3-markdown-it'
  import MarkdownEmoji from 'markdown-it-emoji'
  import Toggle from '../../components/Toggle.vue'
  import Button from '../../components/Button.vue'
  import Dialog from '../../components/Dialog.vue'
  import 'highlight.js/styles/atom-one-dark.css'

  export default defineComponent({
    name: 'editor',
    components: {
      Toggle,
      Button,
      Dialog,
      Markdown,
    },
    setup() {
      const route = useRoute()
      const { go } = useRouter()
      const { state, commit, dispatch } = useStore()

      const Dialog = ref(null)
      const plugins = ref([{ plugin: MarkdownEmoji }])

      let postId = ''
      let draftId = ''
      let canLeavePage = true
      const isLoading = computed(() => state.loading.isLoading)
      const percentage = computed(() => state.loading.percentage)
      const mainMenus = computed(() => Object.keys(state.menu.groupedMenus))
      const subMenus = computed(() => state.menu.groupedMenus[main.value]?.map((menu) => menu.sub))
      const menus = computed(() => state.menu.currentMenus)
      const categories = computed(() => state.menu.currentCategories)

      const main = ref('')
      const sub = ref('')
      const category = ref('')
      const title = ref('')
      const content = ref('')
      const isPublic = ref(true)
      const images = ref([])

      const fileState = reactive({
        fileId: '',
        fileName: '',
        fileUrl: 'http://localhost:3000/uploads/',
        fileIndex: 0,
      })

      const onResetSubMenu = () => {
        sub.value = ''
        category.value = ''
      }

      const onUpdateIsPublic = (state) => {
        isPublic.value = state
      }

      const updateDraft = async () => {
        const payload = {
          menu: menus.value?.shift()?._id,
          title: title.value,
          content: content.value,
          category: category.value,
          isPublic: isPublic.value,
          thumbnail: fileState.fileId,
        }
        await dispatch('draft/updateDraft', { draftId, payload })
      }

      const onUpdatePost = async () => {
        if (!main.value || !sub.value) return alert('게시글을 삽입할 메뉴를 선택해야 합니다.')
        
        onUpdateCanLeavePage(true)
        const payload = {
          menu: menus.value?.shift()._id,
          title: title.value,
          content: content.value,
          category: category.value,
          isPublic: isPublic.value,
          images: images.value.map((image) => image._id),
          thumbnail: fileState.fileId,
        }

        const { success } = route.query.id
          ? await dispatch('post/updatePost', { postId, payload })
          : await dispatch('post/createPost', payload)
        
        if (success && route.query.id) {
          go(-1)
        }
      }

      const onUploadImages = async (event) => {
        const formData = new FormData()
        Object.values(event.target.files).forEach((file) => formData.append('images', file))

        const response = route.query.id
          ? await dispatch('draft/updateDraft', { draftId, payload: formData })
          : await dispatch('post/updatePost', { postId, payload: formData })
        
        if (response.success && response.post) {
          images.value = images.value.concat(response.post.images)
        }
        if (response.success && response.draft) {
          images.value = images.value.concat(response.draft.images)
        }
      }

      const onDeleteImage = async (file) => {
        await dispatch('draft/deleteFile', { draftId, imageId: file._id })
      }

      const onSelectImage = (file, index) => {
        if (file) {
          fileState.fileId = file._id
          fileState.fileName = file.serverFileName
          fileState.fileUrl = fileState.fileUrl + fileState.fileName
          fileState.fileIndex = index
        }
      }

      const onInsertImage = () => {
        let textarea = document.body.querySelector('#content-el')
        let start = textarea.value.substring(0, textarea.selectionStart)
        let end = textarea.value.substring(textarea.selectionEnd, textarea.value.length)

        const innerText = `!` + `[` + `${fileState.fileName}` + `]` + `(` + `${fileState.fileUrl}` + `)\n`
        content.value = start + innerText + end
        textarea.focus()
      }

      const onClearImages = () => {
        images.value = []
      }

      const onUpdateCanLeavePage = (bool) => {
        canLeavePage = bool
      }

      const unloadEvent = (event) => {
        if (!canLeavePage) {
          event.preventDefault()
          event.returnValue = ''
        }
      }
      
      const setInitData = (post) => {
        const menu = state.menu.menus.find((menu) => menu._id === post.menu)
        main.value = menu?.main ?? ''
        sub.value = menu?.sub ?? ''
        title.value = post?.title
        content.value = post?.content
        isPublic.value = post?.isPublic
        images.value = [...post?.images]
        onSelectImage(images.value.shift(), 0)
      }

      const autoSave = route.query.id ? setInterval(onUpdatePost, 1000 * 60 * 10) : setInterval(updateDraft, 1000 * 60 * 10)

      watchEffect(() => {
        commit('menu/SET_CURRENT_MENUS', { main: main.value, sub: sub.value })
        commit('menu/SET_CURRENT_CATEGORIES', menus.value)
      })

      watchEffect(() => {
        onSelectImage(images.value.shift(), 0)
      })

      onBeforeMount(async () => {
        if (route.query.id) {
          const { post } = state.post.post._id ? state.post : await dispatch('post/getPost', route.query.id)
          postId = post._id
          setInitData(post)
          return
        }
        
        const { draft } = await dispatch('draft/getDraft')
        if (draft) {
          const ok = await Dialog.value.show({ 
            title: '임시 저장된 게시물을 불러올깝쇼?',
            message: '루비는 동그랗습니다...',
            okButton: '불러오기',
            cancelButton: '아니요, 새로 작성할래요!'
          })
          if (ok) {
            draftId = draft._id
            setInitData(draft)
          }
          else {
            const { draft } = await dispatch('draft/createDraft')
            draftId = draft._id
            setInitData(draft)
          }  
        }
        else {
          const { draft } = await dispatch('draft/createDraft')
            draftId = draft._id
            setInitData(draft)
        }
      })

      onMounted(() => {
        window.addEventListener('beforeunload', unloadEvent)
      })

      onUnmounted(async () => {
        window.removeEventListener('beforeunload', unloadEvent)
        clearInterval(autoSave)
        commit('draft/SET_DRAFT', {})
      })

      onBeforeRouteLeave(async (to, from, next) => {
        if (canLeavePage) next()
        else {
          const ok = await Dialog.value.show({
            title: '현재 페이지에서 나가시겠습니까?',
            message: '작성된 내용은 떠나갈것입니다...',
            okButton: '나가기',
            cancelButton: '취소',
          })
          ok ? next() : next(false)
        }
      })

      return {
        Dialog,
        plugins,
        mainMenus,
        subMenus,
        menus,
        categories,
        main,
        sub,
        category,
        title,
        content,
        isPublic,
        images,
        fileState,
        isLoading,
        percentage,
        onResetSubMenu,
        onUpdateCanLeavePage,
        onUpdateIsPublic,
        onUpdatePost,
        onUploadImages,
        onInsertImage,
        onClearImages,
        onDeleteImage,
        onSelectImage,
      }
    },
  })
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .editor {
    .wrap_select_toggle {
      display: flex;
      flex-direction: row;
      align-items: center;

      .select {
        display: flex;
        flex-direction: row;
        width: 90%;

        div {
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #ddd;
          width: 27rem;
          border-radius: 3.2rem;
          font-size: 1.4rem;
          margin-right: 2.4rem;

          select {
            width: calc(27rem - 4.8rem);
            font-size: 1.4rem;
            color: var(--text-light);
            height: 4.8rem;
            background-color: transparent;
            font-family: 'Roboto', 'Noto Sans KR', sans-serif;
            text-transform: uppercase;
          }

          &:last-child select > option {
            text-transform: none;
          }
        }
      }

      .wrap_toggle {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 10%;
      }
    }

    .wrap_title_file-add-btn {
      display: flex;
      flex-direction: row;
      align-items: center;
      border: 1px solid #ddd;
      margin: 2.4rem 0 1.6rem;
      border-radius: 3.2rem;
      width: 100%;
      height: 4.8rem;
      padding: 0 2.4rem;

      .title {
        width: 85%;
        input {
          color: var(--text-light);
          font-size: 1.4rem;
          width: 100%;
        }

        input::placeholder {
          color: var(--text-light);
          font-size: 1.4rem;
        }
      }

      .images_add_btn {
        width: 15%;
        display: flex;
        justify-content: flex-end;

        label.upload_label {
          font-size: 1.2rem;
          line-height: 1.8rem;
          cursor: pointer;
          text-transform: uppercase;
          letter-spacing: 0.1rem;
          color: var(--text-light);
          position: relative;
          padding: 0 0 0 3.2rem;

          &::before {
            content: '';
            position: absolute;
            left: 0;
            width: 0.01rem;
            height: 1.6rem;
            background-color: var(--border-dark);
          }
        }

        input#upload_input {
          display: none;
        }
      }
    }

    .content {
      display: flex;
      flex-direction: row;

      textarea,
      .markdown {
        width: 50%;
        min-height: 48rem;
        border-radius: 3.2rem;
        border: 1px solid #ddd;
        padding: 2.4rem;
        font-size: 1.4rem;
        color: var(--text-light);
      }

      textarea {
        margin: 0 2.4rem 0 0;
        resize: vertical;
      }

      textarea::placeholder {
        color: var(--text-light);
        font-size: 1.4rem;
      }
    }

    .images {
      margin: 0 0 -1.2rem 0;
      ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        margin: calc(3.2rem - 1.2rem) 0 0;

        li {
          width: 7.4rem;
          height: calc(7.4rem + 1.6rem);
          margin: 1.2rem 2.4rem 0 0;

          &:nth-child(12n + 0) {
            margin: 1.2rem 0;
          }

          .wrap_thumbnail {
            position: relative;
            img {
              width: 100%;
              height: 7.4rem;
              border-radius: 2.4rem;
              border: 2px solid #ddd;
              object-fit: cover;
              padding: 0.2rem;

              &.selected_thumbnail {
                border: 2px solid var(--primary-dark);
              }
            }
            .image_del_btn {
              position: absolute;
              bottom: -0.6rem;
              right: -0.6rem;
              color: var(--secondary-dark);

              i {
                font-size: 2.2rem;
              }

              .material-symbols-outlined {
                font-variation-settings: 'FILL' 1, 'wght' 400, 'GRAD' 0, 'opsz' 48;
              }
            }
          }
        }
      }
    }

    .wrap_btns {
      display: flex;
      flex-direction: row;
      margin: 3.2rem 0 0;

      .wrap_left {
        width: 50%;
        display: flex;
        align-items: center;

        .btn_image_clear {
          margin: 0 0 0 1.2rem;
        }
      }

      .wrap_right {
        width: 50%;
        display: flex;
        justify-content: flex-end;

        .wrap_isLoading {
          display: flex;
          align-items: center;
          margin: 0 2.4rem 0 0;

          .isLoading {
            display: flex;
            align-items: center;
            color: var(--text-light);
          }
          .isLoading-enter-active,
          .isLoading-leave-active {
            transition: opacity 5s ease;
          }

          .isLoading-enter-from,
          .isLoading-leave-to {
            opacity: 0;
          }
        }

        .wrap_auto-save {
          display: flex;
          align-items: center;
          margin: 0 0 0 2.4rem;

          #auto-save {
            width: 1.6rem;
            height: 1.6rem;
          }
          label {
            margin: 0 2.4rem 0 1.2rem;
            font-size: 1.3rem;
            color: var(--text-dark);
          }
        }
      }
    }
  }
</style>
